/* HomePage.css */

.navbar {
  display: flex;
  align-items: center;
  background-color: #1f1e1e;
  padding: 10px;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar li {
  margin: 0 15px;
  position: relative;
}

.navbar a, .navbar span {
  color: white;
  text-decoration: none;
}

.navbar a:hover, .navbar span:hover {
  text-decoration: underline;
}

.logo {
  width: 50px;
  height: 50px;
  margin-right: 20px;
}

.submenu {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #444;
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  width: max-content;
}

.submenu li {
  margin: 0;
}

.submenu a {
  color: white;
  padding: 10px;
}

.submenu a:hover {
  background-color: #555;
}

.about-section {
  background-color: black;
  color: white;
  padding: 20px;
}
