.products-container {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    justify-content: center;
    padding: 16px;
    background-color: #1f1e1e;
  }
  
  .product-card {
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 16px;
    text-align: center;
    width: calc(100% / 4 - 16px); /* Initially, each product occupies 25% of the width */
    max-width: 300px;
  }
  
  .product-card img {
    width: 100%;
    height: auto;
  }
  
  .product-card h3 {
    margin: 16px 0 8px;
  }
  
  .product-card p {
    color: #333;
    font-size: 1.2em;
  }
  
  @media (max-width: 1200px) {
    .product-card {
      width: calc(100% / 3 - 16px); /* Reduce to 3 products per row on screens up to 1200px */
    }
  }
  
  @media (max-width: 900px) {
    .product-card {
      width: calc(100% / 2 - 16px); /* Reduce to 2 products per row on screens up to 900px */
    }
  }
  
  @media (max-width: 600px) {
    .product-card {
      width: calc(100% - 32px); /* Reduce to 1 product per row on screens up to 600px */
    }
  }
  